@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.MuiBottomNavigation-root{box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)}

.HomeLoggedIn{position:fixed;top:0;bottom:0;left:0;right:0;height:auto;display:flex;flex-direction:column;align-items:center;justify-content:center}.HomeLoggedIn a{margin:1rem;width:12rem}

.HomeNotLoggedIn{display:flex;height:100vh;flex-direction:column;justify-content:center;align-items:center}.HomeNotLoggedIn--info{margin:0 1rem;padding:1rem;background-color:#d7d7d7e8;border-radius:5px}@media only screen and (min-width: 540px){.HomeNotLoggedIn--info{margin:4rem 2rem 0}}@media only screen and (min-width: 960px){.HomeNotLoggedIn--info{width:30rem;margin:8rem 10rem 0}}.HomeNotLoggedIn--info img{width:15rem}@media only screen and (min-width: 540px){.HomeNotLoggedIn--info img{width:12rem}}.HomeNotLoggedIn--info h4{font-weight:700}.HomeNotLoggedIn--info-text{padding:1rem 0;color:#757575}

.startpage{position:fixed;top:0;bottom:0;left:0;right:0;background:url(/static/media/maddi-bazzocco-waNAJOI7Jz8-unsplash.8947ef88.jpg) no-repeat center center;background-size:cover}

.AuthPage{margin:7rem 3rem}.AuthPage form{display:flex;flex-direction:column}.AuthPage form .MuiFormControl-root{padding-top:1.2rem}.AuthPage form .MuiFormControl-root:first-child{padding-top:0.3rem}.AuthPage form .MuiFormControl-root:first-child .MuiFormLabel-root{top:0.3rem}.AuthPage form .MuiFormControl-root .MuiFormLabel-root{top:1.2rem}.AuthPage form .MuiButton-root{margin:2.5rem 0 1rem;text-transform:none}

.event-card .MuiCardActionArea-root{background-color:#d7d7d7e8 !important}.event-card__info{display:flex;flex-direction:column}.event-card__info--date,.event-card__info--location{display:flex;align-items:baseline}.event-card__info--user{display:flex;flex-direction:column}.event-card__info--user-title{padding-top:0.4rem;font-size:0.85rem;color:#318fb5}.event-card__info--user-value{padding-top:0.2rem;font-size:0.9rem}.event-card__info p{margin:0}.event-card__info--value,.event-card__info--title{font-size:0.85rem}.event-card__info--value{padding-left:0.4rem}.event-card__info--title{padding:0.2rem 0;color:#318fb5}


.MuiTypography-root{display:flex}.MuiAvatar-colorDefault{background-color:"#318fb5"}.information{display:flex;margin:0.5rem 0.2rem}.information .text{display:flex;flex-direction:column;padding:0.2rem}.MuiButton-startIcon{margin-bottom:0.1rem}

.header{position:fixed;display:flex;flex-direction:row;justify-content:space-between;align-items:center;top:0;width:100%;height:3.5rem;z-index:2;background-color:#fff;color:#318fb5;box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)}.header--logo img{width:150px;padding:0.2rem 1rem}.header--logo .MuiTypography-root{color:#fff;text-decoration:none;padding:0.2rem 1rem}.header--auth .MuiButton-text{padding:0.2rem 1rem}

/* --------------- */
.page {
  padding: 3.5rem 0;
  background: #318fb5;
}

.page-inverted {
  padding: 3.5rem 0;
  background: #fff;
}

/* --------------- */
.MuiCardActions-spacing {
  justify-content: space-around;
}

.loading-modal {
  position: fixed;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  top: 0;
}

